import React from "react";
import './cotacao_estilo.css';
import { Card, CardBody, CardSubtitle, CardTitle, Container } from "reactstrap";

export default class Cotacao extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            cotacaoMoeda:'',
            controleApi:0
        };
        this.puxaApi = this.puxaApi.bind(this);
        this.trataObjeto = this.trataObjeto.bind(this);
    };
    trataObjeto(obj){
        if(obj.value !== ''){
            const valor = obj.value[0];
            const valorCompra = valor.cotacaoCompra;
            const valorVenda = valor.cotacaoVenda;
            const dataCotacao = valor.dataHoraCotacao;
            this.setState({cotacaoMoeda:{data: dataCotacao,compra: valorCompra,venda: valorVenda}});
        };
    };
    puxaApi(){
        if(this.state.controleApi === 0){
            const date = new Date();
            const url= `https://olinda.bcb.gov.br/olinda/servico/PTAX/versao/v1/odata/CotacaoDolarDia(dataCotacao=@dataCotacao)?@dataCotacao='${String(date.getMonth()+1).padStart(2,'0')}-${String(date.getDate()).padStart(2,'0')}-${date.getFullYear()}'&$top=100&$format=json&$select=cotacaoCompra,cotacaoVenda,dataHoraCotacao`; // padStart(2,'0') faz com que se não houver 2 algarismos no número acrescente um zero antes
            fetch(url)
            .then((json)=>json.json())
            .then((objeto)=>this.trataObjeto(objeto));
            this.setState({controleApi:1});
        };
    };
    render(){
        return(
                <Container fluid className='cotacao'>
                    <div id='card_cotacao'>
                        <Container>
                            <Card>
                                <CardBody>
                                    <CardTitle tag='h3'>
                                        Cotação Real-Dolar
                                    </CardTitle>
                                    <CardSubtitle>
                                        {this.puxaApi()}
                                        <p>Data da Cotacao: {this.state.cotacaoMoeda.data}</p>
                                        <p>Cotacação de Compra: {this.state.cotacaoMoeda.compra}</p>
                                        <p>Cotação de Venda: {this.state.cotacaoMoeda.venda}</p>
                                    </CardSubtitle>
                                </CardBody>
                            </Card>
                        </Container>
                    </div>    
                </Container>
        );
    }
};