import React from "react";
import './calculadora_estilo.css';
import { Col, Container, Row } from "reactstrap";
import Historico from "../Historico/historico";

export default class Calculadora extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            textoTela:['tem alguma coisa'],
            historico:{h1:'',h2:'',h3:''},
            infoHist:{h1:'',h2:'',h3:''},
            controleHist:1
        };
        this.clear = this.clear.bind(this);
        this.escreveTela = this.escreveTela.bind(this);
        this.apagar = this.apagar.bind(this);
        this.executar = this.executar.bind(this);
        this.utilizacaoHist = this.utilizacaoHist.bind(this);
    };
    utilizacaoHist(conta){
        this.clear();
        this.escreveTela(conta);
    };
    clear(){
        const caminho_tela = document.querySelector('#tela')
        caminho_tela.innerHTML = '';
        this.setState({textoTela:''});
    };
    apagar(){
        const caminho_tela = document.querySelector('#tela')
        const stringBruta = caminho_tela.textContent;
        const novaString = stringBruta.substring(0,stringBruta.length-1);
        caminho_tela.innerHTML = novaString;
    };
    escreveTela(string){
        const caminho_tela = document.querySelector('#tela')
        const tratamento = String(string);
        caminho_tela.innerHTML += tratamento   
    };
    executar(){
        const caminho_tela = document.querySelector('#tela')
        if(caminho_tela.textContent !== ''){
            const conteudoTela = String(caminho_tela.textContent)
            const resultado = eval(conteudoTela)
            caminho_tela.innerHTML = resultado;
            switch(this.state.controleHist){
                case 1:
                    this.setState({historico:{...this.state.historico,h1:conteudoTela}})
                    console.log(this.state.historico)
                    this.setState({controleHist:2})
                    break
                case 2:
                    this.setState({historico:{...this.state.historico,h2:conteudoTela}})
                    this.setState({controleHist:3})
                    break
                case 3:
                    this.setState({historico:{...this.state.historico,h3:conteudoTela}})
                    this.setState({controleHist:1})
                    break
            }
        }
    };
    render(){
        return(
            <Container fluid className="calculadora_div">
                <div id="display">
                    <Row xs='1'>
                        <Col>
                            <h2 id='tela'></h2>
                        </Col>            
                    </Row>
                </div>
                <div id='teclas'>
                    <Row xs='1'>
                        <Col>
                            <button className='btn especiais_apagar' onClick={()=>this.clear()}>Clear</button>
                            <button className='btn especiais_apagar' onClick={()=>this.apagar()}>Apagar</button>
                        </Col>
                        <Col>
                            <button className='btn operador' onClick={()=>this.escreveTela('+')}>+</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(1)}>1</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(2)}>2</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(3)}>3</button>
                        </Col>
                        <Col>
                            <button className='btn operador' onClick={()=>this.escreveTela('-')}>-</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(4)}>4</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(5)}>5</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(6)}>6</button>
                            
                        </Col>
                        <Col>
                            <button className='btn operador' onClick={()=>this.escreveTela('*')}>*</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(7)}>7</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(8)}>8</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(9)}>9</button>
                        </Col>
                        <Col>
                            <button className='btn operador' onClick={()=>this.escreveTela('/')}>/</button>
                            <button className='btn teclas' onClick={()=>this.escreveTela(0)}>0</button>
                            <button className='btn teclas' onClick={()=>this.executar()}>=</button>
                        </Col>
                    </Row>
                </div>
                <div id='historico'>
                    <Container>
                        <Row md='3'>
                            <div>
                                <Historico contas={this.state.historico.h1} infosContas={this.state.infoHist.h1} />
                                <button className='btn hist' onClick={()=>this.utilizacaoHist(this.state.historico.h1)}>UTILIZAR</button>
                            </div>
                            <div>
                                <Historico contas={this.state.historico.h2} infosContas={this.state.infoHist.h2} />
                                <button className='btn hist' onClick={()=>this.utilizacaoHist(this.state.historico.h2)}>UTILIZAR</button>
                            </div>
                            <div>
                                <Historico contas={this.state.historico.h3} infosContas={this.state.infoHist.h3} />    
                                <button className='btn hist' onClick={()=>this.utilizacaoHist(this.state.historico.h3)}>UTILIZAR</button>
                            </div>
                        </Row>
                    </Container>
                </div>
            </Container>
        );
    }
}