import React from "react";
import { Card, CardBody, CardSubtitle, CardTitle, Container } from "reactstrap";

export default class Historico extends React.Component{
    render(){
        return(
            <div id='historico'>
                <Container fluid>
                    <Card>
                        <CardBody>
                            <CardTitle tag='h3'>
                                {this.props.contas}
                            </CardTitle>
                            <CardSubtitle tag='p'>
                                {this.props.infosContas}
                            </CardSubtitle>
                        </CardBody>
                    </Card>
                </Container>
            </div>
        );
    };
};